.widgetLibraryPage {
  padding: 24px;
}

@media (min-width: 992px) {
  .widgetLibraryPage {
    padding: 40px;
  }
}

.title {
  margin-bottom: 24px;
  font-size: 24px;
  text-transform: uppercase;
}

.selectsWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  & > div:nth-child(2) {
    margin-top: 16px;
  }
}

@media (min-width: 768px) {
  .selectsWrapper {
    & > div:nth-child(2) {
      margin-top: 0;
    }
  }
}

.selectWrapper {
  width: 100%;
}

@media (min-width: 768px) {
  .selectWrapper {
    width: calc((100% - 24px) / 2);
  }
}

@media (min-width: 992px) {
  .selectWrapper {
    width: calc((100% - 40px) / 2);
  }
}

.subtitles {
  font-size: 18px;
  & > div {
    margin-top: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .subtitles {
    display: flex;
    justify-content: space-between;
    align-items: center;
    & > div {
      width: calc((100% - 24px) / 2);
      min-height: 48px;
    }
  }
}

@media (min-width: 992px) {
  .subtitles {
    & > div {
      width: calc((100% - 40px) / 2);
    }
  }
}

.button {
  margin-left: 8px;
  height: 40px;
  padding: 0 32px;
  font-size: 16px;
  color: #f7f7f7;
  background-color: #41b167;
  border-radius: 4px;
}

.сontent {
  margin-top: 24px;
  display: grid;
  grid-template: "mainArea" auto / 100%;
  column-gap: 40px;
}

@media (min-width: 992px) {
  .сontent {
    grid-template: "mainArea asideArea" auto/ minmax(0, 1fr) minmax(0, 1fr);
  }
}

.main {
  grid-area: mainArea;
  & > div:not(:first-child) {
    margin-top: 24px;
  }
}

.note {
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 500;
}

.filter {
  position: relative;
  margin-bottom: 24px;

  &Icon {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 20px;
  }

  &Input {
    input {
      padding-left: 40px;
    }
  }
}

.templatesWrapper {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;
  column-gap: 24px;
  row-gap: 24px;
}

@media (min-width: 768px) {
  .templatesWrapper {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 1400px) {
  .templatesWrapper {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.widgetsWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 1fr;
  column-gap: 24px;
  row-gap: 24px;
}

@media (min-width: 768px) {
  .widgetsWrapper {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

@media (min-width: 992px) {
  .widgetsWrapper {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 1200px) {
  .widgetsWrapper {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media (min-width: 1400px) {
  .widgetsWrapper {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.aside {
  grid-area: asideArea;
  display: none;
}

@media (min-width: 992px) {
  .aside {
    display: block;
  }
}

.asideSection {
  position: sticky;
  top: 24px;
  padding: 24px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0px 3px 6px #00000029;
}

[dark-theme] {
  .asideSection {
    background-color: #2b324d;
    box-shadow: none;
  }
}

.modal {
  width: calc(100vw - 96px);
  max-width: 672px;
}

.closeButton {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 40px;
  color: #707070;
}
