.header {
  margin-bottom: 24px;
  display: flex;
  align-items: center;

  svg {
    height: 24px;
    margin-right: 16px;
  }

  span {
    font-size: 16px;
    font-weight: 500;
    margin: 0;
  }

  .ifNotAvaliable {
    display: flex;
    align-items: center;
    margin-left: auto;

    svg {
      margin-right: 4px;
    }

    p {
      font-size: 10px;
      font-weight: 500;
      margin-left: 4px;
    }
  }
}

.images {
  display: grid;
  grid-auto-rows: auto;
  row-gap: 24px;
  column-gap: 24px;
}

@media (min-width: 768px) {
  .images {
    grid-auto-flow: column;
  }
}

.imageTitle {
  font-size: 10px;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.image {
  height: 96px;
}

.description {
  margin-top: 24px;
  font-size: 14px;
}

.relatedWidgets {
  margin-top: 24px;

  & > div {
    &:nth-child(1) {
      font-weight: 500;
      font-size: 14px;
    }

    &:nth-child(2) {
      margin-top: 8px;
      display: flex;
      flex-wrap: wrap;
      div {
        margin-right: 16px;
        display: flex;
        flex-direction: column;
        align-items: center;

        &:last-child {
          margin-right: 0;
        }

        svg {
          height: 24px;
        }

        p {
          font-size: 10px;
          margin-bottom: 0;
        }
      }
    }
  }
}
