.template {
  border-radius: 4px;

  & > div {
    height: 100%;
  }

  span {
    display: flex;
    flex-direction: column;
    padding: 4px;
    background: #cece84;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 4px;
    height: 100%;
  }

  .info {
    margin: auto;

    p {
      margin: 24px auto;
      font-size: 16px;
      line-height: 24px;
      font-weight: 300;
      text-align: center;
      text-transform: uppercase;
    }
  }

  .icons {
    display: flex;
    justify-content: center;
    margin: auto;
    margin-bottom: 25px;

    svg {
      width: 100%;
      width: 24px;
      height: auto;
      margin: 0 10px;

      &:nth-last-child() {
        margin-right: 0;
      }
    }
  }
}

@media (min-width: 1440px) {
  .template .info p {
    font-size: 20px;
  }
}

.widget {
  position: relative;

  & > div {
    height: 100%;
  }

  .buttonGroup {
    flex-direction: row;
  }

  span {
    display: flex;
    flex-direction: column;
    background: #ffffff;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 4px;
    padding: 4px;
    height: 100%;
  }

  .info {
    display: flex;
    flex-direction: column;
    height: 100%;

    svg {
      // width: 100%;
      width: 48px;
      margin: 14px auto 30px auto;
    }
  }

  p {
    margin: 0;
    margin-bottom: 30px;
    font-size: 10px;
    font-weight: 500;
    text-align: center;
    margin-top: auto;
  }
}

.buttonGroup {
  display: flex;
  width: 100%;

  button {
    border-radius: 50%;
    box-shadow: 0px 3px 6px #00000029;
    border: 0.5px solid #b5b5b5;
    background-color: white;
    padding: 0;
    width: 16px;
    height: 16px;
  }

  .buttonGroupShowInfo {
    font-size: 13px;
    font-weight: 500;
  }

  .buttonGroupCheck {
    margin-left: auto;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      opacity: 0;
      transition: all 0.2s;
    }

    &.checked {
      background-color: #41b167;
      border: none;

      svg {
        opacity: 1;
        fill: #fff;
      }
    }

    &:hover {
      svg {
        opacity: 1;
      }
    }
  }
}

@media (min-width: 992px) {
  .buttonGroupShowInfo {
    display: none;
  }
}

.unavailable {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 4px;
  svg {
    &:hover {
      & ~ p {
        opacity: 1;
      }
    }
  }

  p {
    opacity: 0;
    transition: all 0.2s;
    margin: 0 0 0 4px;
    font-size: 10px;
    font-weight: 500;
    text-align: center;
  }
}

.template {
  .unavailable {
    color: #242b43;
  }
}

.contentPreviewInModal {
  & > div {
    background-color: transparent !important;
    box-shadow: none !important;
  }
}

[dark-theme] {
  .template {
    span {
      box-shadow: none;
    }

    .info {
      p {
        color: #242b43;
      }

      svg {
        fill: #242b43;
      }
    }
  }

  .widget {
    span {
      background-color: #2b324d;
      box-shadow: none;
    }
  }

  .buttonGroup {
    .buttonGroupCheck {
      svg {
        fill: #242b43;
      }

      &.checked {
        svg {
          fill: white;
        }
      }
    }

    .buttonGroupShowInfo {
      color: black;
    }
  }
}
