.widgetLibraryCreateDashboard {
  & > *:not(:first-child) {
    margin-top: 24px;
  }
}

.title {
  font-size: 18px;
}

.note {
  margin-top: 16px;
  font-size: 14px;
  font-weight: 500;
}

.slider {
  position: relative;
  width: 100%;
  padding: 0 24px;
}

.slickArrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0px 3px 6px #00000029;
  cursor: pointer;
  z-index: 1;
}

[dark-theme] {
  .slickArrow {
    background-color: #313a58;
    box-shadow: none;
  }
}

.prevArrow {
  left: 0;
  svg {
    transform: rotate(90deg);
  }
}

.nextArrow {
  right: 0;
  svg {
    transform: rotate(-90deg);
  }
}

.slide {
  position: relative;
  padding: 8px;
}

.slideText {
  height: 16px;
  line-height: 16px;
  font-size: 10px;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.slideImageWrapper {
  margin: 8px 0;
  height: 96px;
  background: #ffffff;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 4px;
}

[dark-theme] {
  .slideImageWrapper {
    background-color: #313a58;
    box-shadow: none;
  }
}

.slideImage {
  width: 100%;
  height: 100%;
}

