.inputWrapper {
  display: flex;
  & > *:nth-child(1) {
    flex: 1;
  }
}

[dark-theme] {
  .input {
    input {
      background-color: #313a58;
    }
  }
}

.button {
  margin-left: 16px;
  height: 40px;
  padding: 0 32px;
  font-size: 16px;
  color: #f7f7f7;
  background-color: #41b167;
  border-radius: 4px;
}

